import { useState } from 'react';
import windCover from '../../images/wind-in-the-head.png';
import noMoney from '../../images/nomoney.png';
import { CardsWrapper, CardsList, Wrapper, Title } from './BooksList.styled';
import Book from 'components/Book/Book';

const myBooks = [
  {
    id: 1,
    name: 'Вітер у голові',
    type: 'роман',
    cover: windCover,
    published: 'Київ, "ТЕМПОРА", 2025р',
    genre: 'пригоди/фентезі/сатира',
    pages: 312,
    text: 'Щоб трохи підзаробити, львівський велокур’єр Максим бере участь у дослідженні експериментальної вакцини. Під час процедури стається помилка, яка викликає низку наступних шалених подій: впродовж одного насиченого тижня на Максима звалюється величезна купа труднощів та пригод: погоні на велосипедах, бійки, молодіжні субкультури, дівчата, проблеми з грошима, наркотиками, криміналітетом та поліцією, та ще й раптові головні болі й дивні запаморочення. Все стає дедалі більш карколомно, і хлопець намагається розплутати весь той дивний клубок подій. Чи вдасться йому це?',
  },
  {
    id: 2,
    name: 'Вітер у кишенях',
    type: 'буденність',
    cover: noMoney,
    published: 'Самодрук',
    genre: 'життє',
    pages: 666,
    text: 'Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей Немає грошей ',
  },
];

export default function BooksList() {
  const [showModal, setShowModal] = useState(false);
  const [bookNumber, setBookNumber] = useState(null);

  const handleOnClick = () => {
    setShowModal(true);
  };
  const handleOnOverlayClick = () => {
    setShowModal(false);
    setBookNumber(null);
  };

  return (
    <>
      <Wrapper>
        <Title hidden>Мої книги</Title>

        <CardsWrapper>
          <CardsList>
            {myBooks.map(book => {
              const { id, name, cover } = book;
              return (
                <Book
                  key={id}
                  id={id}
                  name={name}
                  cover={cover}
                  myBooks={myBooks}
                  bookNumber={bookNumber}
                  onClick={handleOnClick}
                  onOverlayClick={handleOnOverlayClick}
                  showModal={showModal}
                  setBookNumber={setBookNumber}
                />
              );
            })}
          </CardsList>
        </CardsWrapper>
      </Wrapper>
    </>
  );
}
